(function() {
  'use strict';

  angular
    .module('assign')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('assign')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('assign')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('assign')
    .directive('numbersOnly', numbersOnly);

  /** @ngInject */
  function numbersOnly() {
    return {
      require: 'ngModel',
      link: function (scope, element, attr, ngModelCtrl) {
        function fromUser(text) {
          if (text) {
            var transformedInput = text.replace(/[^0-9]/g, '');

            if (transformedInput !== text) {
              ngModelCtrl.$setViewValue(transformedInput);
              ngModelCtrl.$render();
            }
            return transformedInput;
          }
          return undefined;
        }            
        ngModelCtrl.$parsers.push(fromUser);
        }
    };
  }

})();

(function() {
  'use strict';

  angular
    .module('assign')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($ocModal, $log, toastr, $timeout) {
    var vm = this;

    vm.pdfName = 'Relativity: The Special and General Theory by Albert Einstein';
    vm.pdfUrl = 'assets/pdf/relativity.pdf';
    vm.pdfPassword = 'test';
    vm.scroll = 0;
    vm.loading = 'loading';
    vm.onlyNumbers = /^\d+$/;   
    vm.aadharSubmitted = false;
    vm.signing = false;
    vm.signed = false;

    vm.getNavStyle = function(scroll) {
      if(scroll > 100) return 'pdf-controls fixed';
      else return 'pdf-controls';
    }

    vm.onError = function(error) {
      console.log(error);
    }

    vm.onLoad = function() {
      vm.loading = '';
    }

    vm.onProgress = function (progressData) {
      console.log(progressData);
    };

    vm.showAModal = function() {

      $ocModal.open({
        url: 'partials/modal.html',
        closeOnEsc: false,
        controller: 'registerModalController',
        onClose: function(newCustomer) {
          vm.aadharSubmitted = true;
          vm.signing = true;
          $timeout(function(){
            vm.signing = false;
            vm.signed = true;
          }, 3000);
          toastr.success('Aadhar verified successfully');
        }
      });
    };



    vm.showAModal();
  }
})();

